.wp-block-columns, .wp-block-group {
  max-width: 1340px;
  margin: auto;
}

.menu-list {
  padding: 1.5rem;
  border-bottom: 1px solid #7C809E;
}
.menu-list:hover {
  background: var(--blue-violet-gradient);
  cursor: pointer;
}

:root {
  --headerHeight: 100px;
}

.header-section {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  margin: auto;
  position: relative;
  height: calc(100vh - var(--headerHeight) * 2);
  /**width: calc(100vw - var(--headerHeight));**/
}
.header-section:before {
  content: "";
  height: calc(100vh - var(--headerHeight) * 2);
  width: calc(100vw - var(--headerHeight));
  top: 0;
  left: 0;
  position: absolute;
  background-color: #121D22;
  z-index: -1;
}
.header-section:after {
  content: "";
  width: 100vw;
  height: 1px;
  bottom: 0;
  left: 0;
  position: absolute;
  background-color: none;
  z-index: 3;
}
.header-section .image {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  width: 50%;
  height: calc(100% - 2px);
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.header-section img {
  opacity: 0.7;
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
}
.header-section .image:before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 50%;
  height: 100%;
  z-index: 3;
  background: linear-gradient(0.25turn, rgb(18, 29, 34), rgba(18, 29, 34, 0));
}
.header-section .text-wrapper {
  width: 50%;
  color: white;
}

.wp-block-columns.fake-grid-wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(420px, 1fr));
  grid-template-rows: 400px 400px;
  gap: 1rem;
  margin-bottom: 2em;
}
.wp-block-columns.fake-grid-wrapper.last-element {
  margin-bottom: 100px;
}
.wp-block-columns.fake-grid-wrapper .wp-block-column {
  display: flex;
  padding: 0.5em;
  gap: unset;
}
.wp-block-columns.fake-grid-wrapper .wp-block-column .wp-block-group {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  height: 100%;
  border-radius: 5px;
  overflow: hidden;
}
.wp-block-columns.fake-grid-wrapper .wp-block-column .wp-block-group .wp-block-group__inner-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wp-block-columns.fake-grid-wrapper .wp-block-column .wp-block-group .wp-block-group__inner-container p:first-of-type {
  flex: 1 1 auto;
}

.mitarbeiter {
  width: 100%;
  display: flex;
  gap: 0.5em;
  border-radius: 5px;
  overflow: hidden;
}
.mitarbeiter .teammitglied {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;
  box-shadow: 0 0 5px 5px rgba(30, 30, 30, 0.4);
}
.mitarbeiter .teammitglied .button-certificate {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 20px;
  height: 20px;
  z-index: 99;
  color: #F7F7F7;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mitarbeiter .teammitglied .button-certificate:hover {
  cursor: pointer;
}
.mitarbeiter .teammitglied .button-certificate img {
  height: var(--certificateButtonHeight);
  width: var(--certificateButtonWidth);
  object-fit: contain;
  position: absolute;
  right: 1rem;
  top: 1rem;
  z-index: -1;
  transform: translate(50%, -50%);
}
.mitarbeiter .teammitglied .button-certificate span {
  transform: translate(5%, 7%);
}
.mitarbeiter .teammitglied .bild-team {
  display: flex;
  width: 100%;
  flex: 1 0 auto;
}
.mitarbeiter .teammitglied .bild-team img {
  border-style: none;
  display: block;
  width: 100%;
  object-fit: cover;
}
.mitarbeiter .teammitglied .teammitglied-text-wrapper {
  display: flex;
  padding: 1.5em 1.5em;
  align-items: flex-start;
  background-image: var(--dark-gradient-vertical);
  justify-content: space-between;
  letter-spacing: 1px;
}
.mitarbeiter .teammitglied .teammitglied-text-wrapper h3, .mitarbeiter .teammitglied .teammitglied-text-wrapper h4 {
  text-transform: none;
}
.mitarbeiter .teammitglied .teammitglied-text-wrapper h4 {
  color: #82CBE6;
  font-weight: 400;
  font-size: 1.5rem;
  margin-bottom: 0 !important;
}
.mitarbeiter .teammitglied .buttons-team {
  display: flex;
  gap: 10px;
}
.mitarbeiter .teammitglied .buttons-team .contact-button {
  background-image: var(--blue-violet-gradient);
  width: 40px;
  height: 40px;
  display: flex;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
}
.mitarbeiter .teammitglied .buttons-team .contact-button:hover {
  cursor: pointer;
  background: #101010;
  transition: all 0.2s ease-in-out;
}

.gallery-wrapper {
  display: none;
}

.geschichte-block {
  display: contents;
  margin-bottom: 100px;
}
.geschichte-block .swiper {
  width: 100%;
  height: 100%;
  margin-bottom: 100px;
  padding-right: 290px;
}
.geschichte-block .swiper-slide {
  font-size: 18px;
  background: var(--dark-gradient-vertical);
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
  min-height: 500px;
  padding: 4rem 3rem;
}
.geschichte-block .swiper-slide:first-of-type {
  margin-left: 2rem;
}
.geschichte-block .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.geschichte-block .bg-number {
  font-size: 250px;
  font-weight: 800;
  color: var(--secondary);
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -1;
  opacity: 0.15;
  line-height: 0.7;
}

html, body {
  font-family: Objektiv VF, sans-serif !important;
}

figure.wp-block-image {
  margin: 0 !important;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
figure.wp-block-image img {
  width: 100%;
  max-height: 80vh;
  object-fit: cover;
}

.wp-block-columns {
  gap: 5em;
  margin-bottom: 100px;
}

p {
  font-family: Objektiv VF, sans-serif;
  color: #F7F7F7;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
  line-height: 1.25;
  margin-bottom: 1.25rem !important;
  text-transform: uppercase;
}
h1.wp-block-heading, h2.wp-block-heading, h3.wp-block-heading, h4.wp-block-heading, h5.wp-block-heading, h6.wp-block-heading {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
}
h1.heading, h2.heading, h3.heading, h4.heading, h5.heading, h6.heading {
  font-size: 7.4rem;
  line-height: 1;
}

h1.wp-block-heading, h1.heading {
  color: #DAE2E6;
}

a {
  color: #82CBE6;
  font-weight: 600;
}

.hmax {
  text-transform: uppercase;
  font-weight: 800;
}
.hmax .last-word {
  color: #7C809F;
  display: table;
}

.inner-max-width {
  margin: auto;
  width: 100%;
  max-width: 1340px;
}

.wp-block-group {
  padding: 4rem 2rem;
  background: var(--dark-gradient-vertical);
}

.has-semitransparent-blue-to-violet-gradient-background {
  background: linear-gradient(90deg, rgba(130, 203, 230, 0.4) 0, rgba(124, 128, 159, 0.4) 100%);
}

.has-semitransparent-blue-to-violet-gradient-background {
  background: linear-gradient(90deg, rgba(130, 203, 230, 0.4) 0, rgba(124, 128, 159, 0.4) 100%);
  padding: 30px;
}
.has-semitransparent-blue-to-violet-gradient-background.wp-element-button {
  padding: 10px 55px 10px 25px;
  position: relative;
}

.has-semitransparent-violet-to-blue-gradient-background {
  background: linear-gradient(90deg, rgba(124, 128, 159, 0.4) 0, rgba(130, 203, 230, 0.4) 100%);
  padding: 30px;
}
.has-semitransparent-violet-to-blue-gradient-background.wp-element-button {
  padding: 10px 55px 10px 25px;
  position: relative;
}

a.wp-element-button {
  color: white;
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/** a.wp-element-button:after
 * content: ""
 * width: 15px
 * height: 15px
 * background: url("/app/uploads/2023/06/arrow-small-1.svg")
 * background-repeat: no-repeat
 * background-position: center center
 * position: absolute
 * right: 25px
 * top: 50%
 * transform: translateY(-50%) */
.wp-block-image.overlap-image {
  width: 100%;
  margin: auto;
  position: relative;
  z-index: 99;
  transform: translateY(-100px);
  margin-bottom: -200px;
}
.wp-block-image.overlap-image img {
  display: block;
  margin: auto;
  width: 100%;
  max-width: 1340px;
}

.secondary a {
  position: relative;
  background-color: transparent;
  padding: 0;
}

.secondary a:after {
  content: "";
  width: 150px;
  height: 15px;
  background: url("/app/plugins/custom-scripts54/src/assets/icons/arrow-small.svg");
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  right: -165px;
  top: 50%;
  transform: translateY(-50%);
}

.button-cta {
  display: inline-block;
  background: var(--blue-violet-gradient);
  padding: 12px 24px;
  margin-top: 1.5rem;
  text-transform: uppercase;
  color: #F7F7F7;
}
.button-cta::after {
  content: ">";
  margin-left: 1rem;
  color: #82CBE6;
}
.button-cta:hover {
  cursor: pointer;
  background: #101010;
  transition: all 0.2s ease-in-out;
}